import React, { useEffect, useRef } from 'react';
import * as THREE from 'three'; // Import THREE.js
import BIRDS from 'vanta/dist/vanta.birds.min'; // Import the Vanta effect

const VantaBackground = () => {
  const vantaRef = useRef(null); // Create a ref for the container div

  useEffect(() => {
    // Ensure THREE is available globally
    window.THREE = THREE;

    // Initialize Vanta.js effect
    const vantaEffect = BIRDS({
      el: vantaRef.current,
      mouseControls: true,
      touchControls: true,
      gyroControls: false,
      minHeight: 200.0,
      minWidth: 200.0,
      scale: 1.0,
      scaleMobile: 1.0,
      backgroundColor: 0x0,
      color1: 0x3e0415,
    });

    // Clean up effect on component unmount
    return () => {
      if (vantaEffect) vantaEffect.destroy();
    };
  }, []);

  return <div ref={vantaRef} style={{ width: '100%', height: '100vh' }} />;
};

export default VantaBackground;
